@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
.login-page {
  align-items: center;
  background-image: url(/project-recipes/static/media/ingredients.8db1a86a.jpeg);
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.login-page h1 {
  margin-bottom: 40px;
}

.login-container {
  align-items: center;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}

.login-container > label {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

header {
  background-color: #a5926a;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);
  width: 100vw;
}

header > section:nth-of-type(1) {
  align-items: center;
  display: flex;
  height: 60px;
  justify-content: space-between;
  padding: 10px 20px;
}

header h2 {
  font-size: 20px;
 }

header > section:nth-of-type(2) {
  background-color: rgba(255, 255, 255, 0.8);
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

header label {
  font-family: 'Noto Serif JP', serif;
  margin-right: 10px;
}

.search-radio-container {
  font-size: 14px;
  margin: 10px 0;
}

.search-radio {
  margin-right: 5px;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 20px 0 80px;
}

.card {
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  border: none;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  font-family: inherit;
  justify-content: space-between;
  margin-bottom: 20px;
  width: 160px;
}

.card-image {
  height: 160px;
  width: 160px;
}

.card span {
  font-size: 12px;
  margin-top: 20px;
  padding: 10px;
}

.ingredient-image {
  height: 100;
  margin-top: 10px;
  width: 100px;
}

footer {
  background-color: #a5926a;
  bottom: 0;
  display: flex;
  height: 60px;
  justify-content: space-around;
  position: fixed;
  width: 100vw;
}

.bottom-circle {
  background-color: #a5926a;
  border-radius: 50%;
  bottom: -20px;
  height: 100px;
  position: fixed;
  width: 180px;
  z-index: -10;
}

.relative {
  bottom: 10px;
  position: relative;
}

.fa-icons-footer{
  font-size: 30px;
}
#button-go-back {
  background: white;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 0;
  padding: 2px;
  transition: 0.4s;
  outline: none;
  position: absolute;
  z-index: 1;
  left: 5px;
  top: 5px;
}

.fas-button-go-back {
  font-size: 25px;
  color: #77321a;
}
.carousel {
  display: flex;
  overflow-x: scroll;
}

.carousel-card {
  margin-left: 40px;
}

.details-container {
  margin-bottom: 80px;
}

.details-image-container {
  position: relative;
}

.details-image {
  height: 100vw;
  width: 100vw;
}

.details-image-container > div {
  bottom: 0;
  display: flex;
  padding: 20px;
  position: absolute;
  right: 0;
}

.details-text-container {
  margin-bottom: 30px;
  padding: 10px;
}

.details-text-container > h3 {
  margin: 20px 0 10px;
}

.details-text-container > ul {
  background-color: rgba(255, 255, 255, 0.4);
  font-size: 14px;
    font-family: 'Noto Serif JP', serif;
  padding: 20px 10px;
}

.details-text-container li {
  align-items: center;
  display: flex;
  padding: 5px;
}

.details-text-container > p {
  background-color: rgba(255, 255, 255, 0.4);
  font-family: 'Noto Serif JP', serif;
  font-size: 14px;
  padding: 40px 10px;
  text-align: justify;
}

.ingredient-checkbox {
  margin-right: 5px;
}

.line-through {
  text-decoration: line-through;
}

.youtube-video {
  border: none;
  height: 240px;
  margin-bottom: 40px;
  width: 100vw;
}

.done-card-image {
  height: 150px;
  width: 150px;
}

.done-card {
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.2);
  display: flex;
  font-size: 12px;
  height: 150px;
  margin-bottom: 20px;
}

.done-card > div:nth-child(2) {
  display: flex;
  flex-direction: column;
  flex-grow: 10;
  padding: 5px 0 0 5px;
}

.done-card-list {
  padding: 10px;
}

.share-container {
  align-items: flex-end;
  display: flex;
  flex-grow: 10;
  justify-content: flex-end;
  padding: 10px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #eee7d5;
  color: #312313;
  /* font-family: 'Noto Serif JP', serif; */
  font-family: 'Noto Sans JP', sans-serif;
}

h5 {
  color: #a5926a;
}

button {
  border-radius: 4px;
}

a {
  color: inherit;
  text-decoration: none;
}

.alt-button {
  background-color: #77321a;
  border: none;
  color: #eee7d5;
  font-family: inherit;
  padding: 10px 20px;
}

.alt-button:disabled {
  opacity: 0.6;
}

.category-buttons-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 10px 0;
}

.category-button,
.category-button-alt {
  background-color: #77321a;
  border: none;
  color: #eee7d5;
  height: 48px;
  font-family: inherit;
  margin-bottom: 5px;
  width: 30vw;
}

.category-button-alt {
  background-color: #312313;
  color: #eee7d5;
}

.control-buttons-page {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.control-buttons-page h3 {
  margin-top: 40px;
}

.control-buttons-container {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}

.control-select {
  align-self: center;
  font-family: inherit;
  padding: 5px;
  width: 200px;
}

.control-button {
  background-color: #77321a;
  /* background-image: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0)
  ); */
  border: none;
  color: #eee7d5;
  height: 50px;
  font-family: inherit;
  font-size: 16px;
  margin-bottom: 5px;
  width: 250px;
}

.last {
  margin-top: 100px;
}

.details-button {
  background-color: #77321a;
  border: none;
  bottom: 0;
  color: #eee7d5;
  font-size: 18px;
  height: 50px;
  position: fixed;
  text-transform: uppercase;
  width: 100vw;
}

.details-button:disabled {
  text-decoration: line-through;
}

.svg-button {
  background: transparent;
  border: none;
}

.svg-button-alt {
  align-items: center;
  background: white;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);
  display: flex;
  height: 48px;
  justify-content: center;
  margin-left: 10px;
  width: 48px;
}

.svg-small {
  height: 24px;
  width: 24px;
}

.svg-med {
  height: 32px;
  width: 32px;
}

.svg-large {
  height: 48px;
  width: 48px;
}

.warning-message {
  position: fixed;
  text-align: center;
  top: 20px;
  width: 100vw;
  z-index: 10;
}

.warning-message span {
  background-color: white;
  font-style: italic;
  padding: 10px;
}

.text-input {
  border: 1px solid #eee7d5;
  font-family: inherit;
  margin-top: 5px;
  padding: 5px;
  width: 200px;
}

